import React from 'react';
import formatMoney from '../lib/formatMoney';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const CartItemStyles = styled.li`
`;

// Destructured' props
// const CartItem = ({ cartItem }) =>
// 	<CartItemStyles>
// 		<img width="100" src={cartItem.item.image} alt=""/>
// 	</CartItemStyles>;

const CartItem = props =>
	<CartItemStyles>
		<img width="100" src={props.cartItem.item.image} alt="{props.cartItem.item.title}"/>
		<div className="cart-item-details">
			<h3>{props.cartItem.item.title}</h3>
			<p>
				{formatMoney(props.cartItem.item.price)}
				{' - '}
				<em>{props.cartItem.quantity} &times; {props.cartItem.item.price}</em>
			</p>
		</div>
	</CartItemStyles>;

CartItem.propTypes = {
	cartItem: PropTypes.object.isRequired,
};

export default CartItem;